// --- HELPERS PARA ESTILOS ---
const getCategoryStyles = (cat: string) => {
  switch (cat) {
    case 'art': return { bg: 'bg-amber-50', text: 'text-amber-700', icon: <Palette size={10} />, label: 'Arte' };
    case 'psychology': return { bg: 'bg-blue-50', text: 'text-blue-700', icon: <Brain size={10} />, label: 'Psico' };
    case 'teaching': return { bg: 'bg-emerald-50', text: 'text-emerald-700', icon: <Users size={10} />, label: 'Docencia' };
    default: return { bg: 'bg-gray-100', text: 'text-gray-600', icon: null, label: 'Otro' };
  }
};

